@border:#b2b6bb;
@background: #fff;
@titleColor: #f9f9f9;
body{
  background-color: #f3f3f5;
  font-size: 12px;
  .red{
    color: red;
  }
  .fiter-button{
    background-color: #fb7456;
  }
  .navMenu{
    background-color: #454545;
    a:link, a:visited,a:hover{
      font-size: 16px;
      color: #fff !important;
    }
  }
  .navbar{
    margin-bottom: 0;
    ul{
      li::after{
        content: "";
        width: 1px;
        height: 20px;
        position: absolute;
        top: 15px;
        right: 0;
        background-color: #c2c3c8;
      }
    }
  }
  .view{
    width: 90%;
    height:100%;
    background-color: @background;
    margin: 0 auto;
    padding-top: 20px;
    box-shadow: 0 0 40px #999999;
    //position: absolute;
    //left: 5%;
    .left-menu{
      width: 200px;
      float: left;
      //position: absolute;
      background-color: @background;
      //top: 20px;
      //bottom: 0px;
      //position: absolute;
      transition: 0.7s;
      .menu-item{
        .menu-title{
          width: 100%;
          height: 40px;
          line-height: 40px;
          text-align: center;
          color: #b2b6bb;
          border-bottom: 1px solid @border;
          background-color: @titleColor;
        }
        ul{
          .topMargin{
            margin-top: 20px;
          }
          li{
            width: 200px;
            height: 40px;
            list-style-type: none;
            line-height: 40px;
            padding-left: 65px;
            a{
              width: 100%;
              display: block;
              color: #000;
              text-decoration: none;
              i{
                margin-right: 5px;
              }
            }
          }
          .active{
            background-color: #c4dce6;
          }
        }
      }
    }
    .section{
      //position: absolute;
      //top: 20px;
      //left: 200px;
      //bottom: 0;
      //right: 0;
      float: left;
      background-color: @background;
      .content{
        width: 100%;
        transition:0.7s;
        position:relative;
        z-index:1;
        .content-title{
          width: 100%;
          height:40px;
          line-height: 40px;
          font-size: 16px;
          border-bottom: 1px solid @border;
          position: relative;
          float: left;
          padding-left: 20px;
        }
        .content-body{
          position: relative;
          padding-top: 60px;
          margin: 0 10px;
          .nav-tabs{
            li{
              cursor: pointer;
            }
          }
          .tab-content{
            padding-top: 20px;
            .form-element{
              display: inline-block;
              margin: 20px 0 0 20px;
              label{
                font-weight: normal;
              }
              input{
                width: 100px;
              }
            }
            li{
              border: 1px solid #ececec;
              list-style: none;
              margin-top: 20px;
              .top{
                height:40px;
                background-color: #f1f1f1;
                line-height: 40px;
                .time{
                  font-weight: bold;
                  margin-right: 10px;
                }
                .top-item{
                  display: inline-block;
                  margin: 0 50px;
                }
              }
              .bottom{
                height:110px;
                padding-top: 10px;
                img{
                  width: 100px;
                  height: 100px;
                  float: left;
                }
                .product-desc{
                  float: left;
                  margin-left: 10px;
                  word-wrap:break-word;
                  width:250px;
                }
                .task-num{
                  float: left;
                  width: 80px;
                }
                .price{
                  font-weight: bold;
                }
                .rep{
                  margin-top: 20px;
                }
              }
            }
          }
        }
      }
    }
  }
  .require{
    &::after{
      content: "*";
      color: red;
    }
  }
}